
#homePage #header {
  background: #f45440;
  margin: 15px 2%;
  width: 96%;
  border-radius: 5px;
  line-height: 45px;
  font-size: 18px;
  color: #fff;
  text-align: center;
}
#homePage #search {
  position: absolute;
  border: 1px solid #ccc;
  line-height: 35px;
  border-radius: 5px;
  margin-left: 2%;
  outline: none;
  width: 70%;
}
#homePage #searchBt {
  position: absolute;
  display: inline-block;
  width: 24%;
  left: 73%;
  text-align: center;
  font-size: 18px;
  background: #ffd200;
  color: #f45440;
  line-height: 35px;
  border-radius: 5px;
  margin-left: 1%;
}
#homePage #submlit {
  position: fixed;
  width: 20%;
  height: 40px;
  right: 0px;
  bottom: 70px;
  color: #f45440;
  background: #ffd200;
  line-height: 40px;
  text-align: center;
  z-index: 99;
  border-radius: 20px 0 0 20px;
  font-size: 15px;
}
#homePage #submlit span {
  display: inline-block;
  background: #f45440;
  color: #ffd200;
  width: 20px;
  height: 20px;
  line-height: 20px;
  border-radius: 10px;
  font-size: 11px;
}
#homePage #submlit label {
  display: inline-block;
  margin-left: 3px;
  font-size: 15px;
  line-height: 40px;
}
#content ul li {
  list-style: none;
  position: relative;
  height: 90px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
}
#content ul li > img {
  width: 80px;
  border: 1px solid gray;
  position: absolute;
  margin-left: 5px;
}
#content ul li .name {
  display: inline-block;
  margin-left: 90px;
  width: 50%;
  line-height: 40px;
  position: absolute;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 15px;
  color: #333;
}
#content ul li .price {
  position: absolute;
  margin-left: 90px;
  margin-top: 40px;
  line-height: 40px;
  color: #f45440;
  font-size: 11px;
}
#content ul li .price span {
  font-size: 18px;
}
#content ul li .sub_add_view {
  position: absolute;
  width: 104px;
  height: 30px;
  right: 10px;
  bottom: 10px;
}
#content ul li .sub_add_view span {
  display: inline-block;
  width: 0px;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 0px;
  top: 0px;
}
#content ul li .sub_add_view span img {
  width: 30px;
  position: absolute;
}
#content ul li .sub_add_view .add {
  margin-left: 70px;
}
#content ul li .sub_add_view label {
  line-height: 30px;
  display: inline-block;
  width: 40px;
  text-align: center;
  color: #333;
  left: 30px;
  position: absolute;
  font-size: 14px;
}




